.header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  color: rgb(255, 255, 214);
  border-bottom: 1px solid antiquewhite;
  height: 10vh;

  .text-logo {
    display: flex;
    height: 100%;
    align-items: center;
    line-height: 1.9rem;

    .logo {
      height: 100%;
      width: auto;
    }

    .logo-container {
      display: flex;
      flex-direction: column;

      .requestum {
        font-size: 2.5rem;
      }
    }
  }
}


@media (max-width: 680px) {
  .header-container {
    flex-direction: column;
    justify-content: center;
    height: 20vh;

    .head-text {
      margin-bottom: 1rem;
      font-size: 1rem;
    }

    .text-logo {
      line-height: 4rem;

      .logo-container {
        .text {
          line-height: 1.5rem;
          letter-spacing: 0.17rem;
        }

        .requestum {
          font-size: 3rem;
        }
      }
    }
  }
}
